import * as React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

const Header = ({ siteTitle, fixedHeader }) => {

  if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)){
    // true for mobile device
    fixedHeader = true
  }

  const handleToggleMenu = (e) => {
    let buttonEle = e.target;
    if(buttonEle.getAttribute('aria-expanded') === 'false')
      buttonEle.setAttribute('aria-expanded', true);
    else buttonEle.setAttribute('aria-expanded', false);
    buttonEle.classList.toggle('collapsed');
    let navbarMenu = document.getElementById('navbarSupportedContent')
    navbarMenu.classList.toggle('show')
  }

  return (
    <header
      className={`main-header ${fixedHeader ? 'fixed-header' : ''}`}
    >
      <div className="container pl-0 pr-0">
        <div className="header-con">
          <nav className="navbar navbar-expand-lg navbar-light p-0">
            <Link to="/" className="navbar-brand p-0">
              <StaticImage src="../images/phanh-logo-1.png" loading="eager" alt={siteTitle} className="img-fluid" />
            </Link>
            <button onClick={handleToggleMenu} className="navbar-toggler p-0 collapsed" 
              type="button" 
              data-toggle="collapse" 
              data-target="#navbarSupportedContent" 
              aria-controls="navbarSupportedContent" 
              aria-expanded="false" 
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon" />
              <span className="navbar-toggler-icon" />
              <span className="navbar-toggler-icon" />
            </button>
            <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item active pl-0">
                  <Link className="nav-link p-0 is-active" to="/">Trang chủ<span
                    className="sr-only">(current)</span></Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link p-0" to="#service-con">Keyworks</Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link p-0" to="#blog">Bài viết</Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link p-0" to="#Portfolio">Sản phẩm</Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link p-0" to="#testimonials">Về Phanh</Link>
                </li>
              </ul>
              <div className="d-inline-block contact">
                <Link to="#Contact">Liên hệ</Link>
              </div>
            </div>
          </nav>
        </div>
      </div>
    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
