/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import { useEffect, useState } from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"

import { useStaticQuery, graphql, Link } from "gatsby"
import Header from "./header"
import "./layout.css"
import BannerSection from "./banner-section"
import FooterSection from "./weight-footer-section"

const Layout = ({ children, bannerTop }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const [fixedHeader, setFixedHeader] = useState(false);
  const isSSR = typeof window === `undefined`

  useEffect(() => {
    if(!isSSR) {
      const WOW = require('wowjs')
      new WOW.WOW({
        live: false
      }).init();
    }    
  }, [])

  useEffect(() => {
    const scrollEvent = () => setFixedHeader(window.scrollY >= 113)
    if(!isSSR) {    
      window.addEventListener("scroll", scrollEvent)
    }

    return () => window.removeEventListener("scroll", scrollEvent)
  }, []) 

  return (
    <>
    <Helmet>      
        <link 
          href="https://pro.fontawesome.com/releases/v5.10.0/css/all.css" 
          rel="stylesheet" 
          type="text/css" 
          integrity="sha384-AYmEC3Yw5cVb3ZcuHtOA93w35dYTsvhLPVnYs9eStHfGJvOvKxVfELGroGkvsg+p" 
          crossorigin="anonymous"
        />
        <link 
          href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css" 
          rel="stylesheet" 
          type="text/css"
        />
      </Helmet>
      
      {/*header-and-banner-section*/}
      <div className="header-and-banner-con w-100 float-left position-relative">
        <div className="header-and-banner-inner-con">
          <Header fixedHeader={fixedHeader} siteTitle={data.site.siteMetadata?.title || `Title`} />
          {bannerTop && <BannerSection fixedHeader={fixedHeader} />}        
        </div>
      </div>
      {/*header-and-banner-section*/}

      {children}

      {/*weight footer section*/}
      <FooterSection />
      {/*weight footer section*/}
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
